.header {
  width: 100%;
  height: 48px;
  background-color: $black;

  @include tablet {
    height: 60px;
  }
}

.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.logo {
  transition: transform $delay $cubic;

  &:hover,
  &:focus {
    transform: scale(1.1);
  }

  @include tablet {
    display: flex;
    align-items: center;
    gap: 8px;
  }
}

.logo-img {
  width: 32px;
  height: 32px;
  @include tablet {
    width: 48px;
    height: 48px;
  }
}

.logo-text {
  display: none;

  font-size: 24px;
  line-height: 1.16;
  text-align: center;

  color: $text-color-light;

  transition: color $delay $cubic;

  .logo:hover &,
  .logo:focus & {
    color: $accent-color;
  }

  @include tablet {
    display: block;
  }
}

.header-nav-list {
  display: none;
  @include tablet {
    display: flex;
    gap: 32px;
    justify-content: center;
    align-items: center;
  }
}

.header-nav-link {
  position: relative;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.16;

  text-transform: uppercase;
  color: $sub-title-color;
  transition: color $delay $cubic;

  &:hover,
  &:focus {
    color: $accent-color;
  }
}

.header-nav-link::after {
  content: '';
  position: absolute;

  bottom: -15px;
  right: 0;
  width: 0%;

  height: 4px;
  background-color: $accent-color;
  border-radius: 2px;

  transition: all $delay $cubic;
}

.header-nav-link:hover::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -15px;
  width: 100%;

  height: 4px;
  background-color: $accent-color;
  border-radius: 2px;
}
